body{
    margin: 0px;
    padding: 0px;
    
}
.bg_nav{
    background-color: white;
}
.menu{
    font-size: 25px;
}
.nav_active{
    font-weight: bold;
    border-bottom: 3px solid black;
}
.home_div{
    margin-top: 10%;
}
.btn_get_started{
    font-weight: 500;
    font-size: 16px;
    letter-spacing: 1px;
    padding: 10px 30px;
    border: 2px solid blueviolet;
    border-radius: 30px;
}
.btn_get_started:hover{
    background-color: blueviolet;
    color: white;
    text-decoration: none;
    transition-duration:0.8s ;
}
.animated{
    width: 100%;
    animation:up-down 2s ease-in-out infinite alternate-reverse both;
}
@-webkit-keyframes up-down{
    0%{
        transform:translateY(10px);
    }
    100%{
        transform:translateY(-10px);
    }
}
@keyframes up-down{
    0%{
        transform:translateY(10px);
    }
    100%{
        transform:translateY(-10px);
    }
}
.brand{
    color: rgb(43, 183, 226);
}
.call{
    font-size: 20px;
    border-radius: 30px;
    padding: 10px 30px;
}
.txt{
    background-color: blueviolet;
    background-image: linear-gradient(to bottom right, blueviolet,white);
}